export const pswTipText = [
	{
		tip: '8~14',
		isok: (psw='') => {
			const pswChartLen = getStrChartLen(psw);
			return pswChartLen <= 14 && pswChartLen >= 8
		},
	},
	{
		tip: '/2',
		isok: (psw='') => /(?=.*[a-zA-Z0-9])(?=.*[^a-zA-Z0-9]).{2,14}/.test(psw),
	},
	{
		tip: '',
		isok: (psw='') => psw.length > 0 && !(/[\s\u4e00-\u9fa5]/.test(psw)),
	}
]

